body {
  font-size: 1.6rem; }

html.preload.js body {
  animation: -cdm-preload 8s steps(1, end) 0s 1 normal both; }

html.preload.js #cdm-loader {
  animation: -cdm-loader-preload 8s steps(1, end) 0s 1 normal both; }

html.preload.js * {
  transition: none !important; }

#page {
  visibility: hidden; }

html.no-js #cdm-loader {
  display: none; }

.toAnimate {
  opacity: 0; }

.content,
.wp-block-group__inner-container {
  width: 100%;
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  display: block;
  margin-left: auto;
  margin-right: auto; }

.alignwide {
  width: 100%;
  max-width: 1700px;
  padding-left: 15px;
  padding-right: 15px;
  display: block;
  margin-left: auto;
  margin-right: auto; }

.alignfull {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px; }

#cdm-loader {
  display: inline-block;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 6.4rem;
  height: 6.4rem;
  visibility: hidden;
  z-index: 99999; }
  #cdm-loader div {
    position: absolute;
    top: 2.7rem;
    width: 1.1rem;
    height: 1.1rem;
    border-radius: 50%;
    background: #000;
    animation-timing-function: cubic-bezier(0, 1, 1, 0); }
    #cdm-loader div:nth-child(1) {
      left: 0.6rem;
      animation: lds-ellipsis1 0.6s infinite; }
    #cdm-loader div:nth-child(2) {
      left: 0.6rem;
      animation: lds-ellipsis2 0.6s infinite; }
    #cdm-loader div:nth-child(3) {
      left: 2.6rem;
      animation: lds-ellipsis2 0.6s infinite; }
    #cdm-loader div:nth-child(4) {
      left: 4.5rem;
      animation: lds-ellipsis3 0.6s infinite; }

@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0); }
  100% {
    transform: scale(1); } }

@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1); }
  100% {
    transform: scale(0); } }

@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0); }
  100% {
    transform: translate(19px, 0); } }

@keyframes -cdm-preload {
  from {
    visibility: hidden; }
  to {
    visibility: visible; } }

@keyframes -cdm-loader-preload {
  from {
    visibility: visible; }
  to {
    visibility: hidden; } }
